.paperContainer{
    min-width: 390px;
    min-height: 468px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    align-content: center;

    position: relative;
}
.table{
    margin-top: 30px;
    height: 600px;
    width: 70%;
    overflow: visible;
    flex-wrap: wrap;
}

.additionalTitle{
    color: rgba(60, 45, 61, 0.5);
    letter-spacing: 0.1em;
    padding: 0;
    position: absolute;
    top: 20px
}
.mainTitle {
    color: rgba(60, 45, 61, 0.9);
    padding: 0;
    position: absolute;
    top: 70px
}

.content{
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    width: 90%;
   /* background-color: red;*/
    /*min-width: 350px;*/
    padding: 0 50px;
    position: absolute;
    top: 130px;
    bottom:50px;
    justify-content: space-around;

}
.tableContent{
    padding: 0;
    bottom: 20px;
    top: 130px;
    justify-content: space-between;
}


.content p,
.content span,
.content label{
    font-size: 0.9rem;
    color: rgba(0,0,0,0.5);
    text-align: center;
}


